body {
  height: 100vh;
  background-color: #222;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

.container {
  width: 75%;
}

.list-wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 15px;
  display: flex;
}

.list-item {
  width: 30%;
  cursor: pointer;
  background: #fff;
  border: 1px solid #eee;
  margin: 15px;
  padding: 10px;
  box-shadow: 0 0 10px #eee;
}

.list-item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.simple-pagination ul {
  text-align: center;
  margin-left: 10px;
  padding: 0;
  list-style: none;
}

.simple-pagination li {
  margin: 2px;
  display: inline-block;
}

.simple-pagination li a, .simple-pagination li span {
  width: 40px;
  height: 40px;
  color: #252525;
  background-color: #fff;
  border-radius: 4px;
  margin: 5px;
  line-height: 40px;
  text-decoration: none;
  transition: all 1s;
  display: block;
  box-shadow: inset 0 5px 10px rgba(0, 0, 0, .1), 0 2px 5px rgba(0, 0, 0, .5);
}

.simple-pagination li a:hover {
  color: #fff;
  background: #000;
}

.simple-pagination .current {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 1px solid;
}

.simple-pagination .prev.current, .simple-pagination .next.current {
  background: #222;
  border: none;
}

